/*
    Buttons
*/
.btn {
  display: inline-block;
  text-decoration: none;
  appearance: none;
  font-family: inherit;
  font-size: inherit;
  line-height: 3;
  white-space: nowrap;
  padding-left: 0.75em;
  padding-right: 0.75em;
  border-radius: 0.25em; }

.btn-primary {
  background-color: #014d8c; }

.btn--small {
  padding-left: 0.75em;
  padding-right: 0.75em;
  line-height: 2; }

.btn:active {
  background: #333;
  color: #fff; }

